ight: 2.1rem\n  width: 2.1rem\n\n#datepicker-calendar .air-datepicker.-inline- .air-datepicker--content .air-datepicker-body .air-datepicker-body--cells .air-datepicker-cell.-selected-.-range-from-\n  height: 2.1rem\n  width: 2.1rem\n\n  &::after\n    top: 0\n    \n#datepicker-calendar .air-datepicker.-inline- .air-datepicker--content .air-datepicker-body .air-datepicker-body--cells .air-datepicker-cell.-selected-.-range-to- \n  height: 2.1rem\n  width: 2.1rem\n\n#datepicker-calendar .air-datepicker.-inline- .air-datepicker--buttons .air-datepicker-buttons\n  display: flex\n  justify-content: space-between\n\n.air-datepicker\n  --adp-font-family: 'Montserrat'\n  --adp-font-size: 14px\n  --adp-background-color: #fff\n  --adp-background-color-hover: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%) !important\n  --adp-background-color-active: #eaeaea\n  --adp-background-color-in-range: rgba(92, 196, 239, .1)\n  --adp-background-color-in-range-focused: rgba(92, 196, 239, .2)\n  --adp-background-color-selected-other-month-focused: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%) !important\n  --adp-background-color-selected-other-month: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%) !important\n  --adp-color: #4a4a4a\n  --adp-color-secondary: #9c9c9c\n  --adp-accent-color: #4eb5e6\n  --adp-color-current-date: var(--adp-accent-color)\n  --adp-border-radius: 4px\n  --adp-nav-color-secondary: var(--adp-color-secondary)\n  --adp-cell-background-color-selected: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n  --adp-cell-background-color-selected-hover: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n  --adp-cell-background-color-in-range: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n  --adp-cell-background-color-in-range-hover: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n  --adp-cell-border-color-in-range: var(--adp-cell-background-color-selected)\n  --adp-btn-color: rgba(188, 156, 255, 1) !important\n  --adp-border-color-inner: transparent!important\n  --adp-nav-color-secondary: var(--adp-color-secondary)\n  --adp-color-secondary: rgba(31, 32, 65, 1)!important\n  --adp-accent-color: rgba(31, 32, 65, 0.5) !important\n  --adp-color-current-date: var(--adp-accent-color)\n  --adp-cell-background-color-selected: #6FCF97\n  --adp-cell-border-radius: 50%\n  --adp-cell-background-color-in-range-hover: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n\n.air-datepicker-nav--title\n  font-weight: 700\n  font-size: 1.1875rem\n  color: rgba(31, 32, 65, 1)\n\n.air-datepicker-nav--title i\n  color: var(--adp-nav-color-secondary)\n\n.air-datepicker-nav--action svg\n  width: 1.5em\n  height: 1.5em\n\n.air-datepicker-nav--action path\n  fill: #BC9CFF !important\n  stroke: transparent!important\n\n.air-datepicker-body--day-names\n  margin: .625em 0 .5em!important\n\n.air-datepicker-body--day-name\n  color: #BC9CFF !important\n  font-size: .75rem!important\n  text-transform: none!important\n  font-weight: 700\n\n.air-datepicker-cell\n  color: rgba(31, 32, 65, 0.5)\n  font-size: .75rem\n\n  &:hover\n    background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%)\n\n.air-datepicker-cell.-selected-, .air-datepicker-cell.-range-from- \n  color: #ffffff !important\n  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%) !important\n  border-radius: 50% !important\n\n.air-datepicker-cell.-range-from- \n  position: relative\n\n  &::after\n    content: ''\n    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)\n    position: absolute\n    right: 0\n    width: 1.3rem\n    height: 2rem\n    z-index: -45\n    opacity: 0.35\n\n.air-datepicker-cell.-current-\n  background: var(--adp-background-color-hover)\n  border-radius: 50%\n  font-weight: 700\n  width: 2.1rem\n  height: 2.1rem\n\n.air-datepicker-cell.-focus-\n  background: var(--adp-background-color-hover)\n  border-radius: 50%\n  font-weight: 700\n  color: #ffffff !important\n\n.air-datepicker-cell.-range-from- \n  border: 1px solid var(--adp-cell-border-color-in-range)\n\n.air-datepicker-cell.-in-range- \n  background: var(--adp-cell-background-color-in-range)\n  border-radius: 0\n  opacity: 0.35\n  color: rgba(31, 32, 65, 1)\n\n  &:hover\n    background: var(--adp-cell-background-color-in-range-hover)\n\n.air-datepicker-cell.-range-to- \n  border: 1px solid var(--adp-cell-border-color-in-range)\n  background-color: var(--adp-cell-background-color-in-range)\n  border-radius: 50% !important\n  position: relative\n\n  &::after\n    content: ''\n    background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)\n    position: absolute\n    right: .8125rem\n    width: 1.85rem\n    height: 2rem\n    z-index: -45\n    opacity: 0.35\n\n.air-datepicker-button \n  color: var(--adp-btn-color)\n  background: rgba(255, 255, 255, 0)\n  font-size: .75rem\n  text-transform: uppercase\n  font-weight: 700\n\n#datepicker_eight, #datepicker_ninth, #datepicker_eleventh, #datepicker_twelfth\n  max-width: 11.3em\n  padding: 1.6em 1em 1.6em 1.15em\n  letter-spacing: 0.08em\n\n.rooms__datepicker-wrap::after\n  top: 1.4em\n  right: 1.1em\n\n#datepicker_fifth, #datepicker_sixth\n  width: 11.4em\n  padding: 1.6em 1em 1.6em 1.15em\n\n.book__datepicker-wrap::after\n  top: 0.9rem\n  right: 0.9rem","@font-face\n    font-family: Montserrat, Arial, sans-serif\n    src: url('../../../assets/fonts/montserrat/montserrat-regular.woff2'), url('../../../assets/fonts/montserrat/montserrat-regular.woff'), url('../../../assets/fonts/montserrat/montserrat-regular.ttf'), url('../../../assets/fonts/montserrat/montserrat-regular.eot')\n    font-weight: 400\n    font-style: normal","@import \"__mixins.sass\"\n@import \"__variables.sass\"\n@import \"../fonts/fonts.sass\"\n\nhtml\n  font-family: 'Montserrat'\n  font-size: 0.87rem\n  color: $dark-blue\n\nbody \n  @include zero-offset()\n  height: 100%\n  background-color: #fff\n  overflow-x: hidden\n\ndiv, p, form, input, a, span, button \n  box-sizing: border-box\n\np\n